.Aside {
  background: #1e213a;
  width: 460px;
  padding: 70px 47px;
  position: relative;
  display: flex;
  flex-direction: column;
}
.closeBtn {
  cursor: pointer;
}
.closeBtn::before {
  content: url('./close-icon.svg');
  position: absolute;
  top: 20px;
  right: 47px;
  transform: scale(1.5);
}
.cloudsBackground {
  position: absolute;
  transform: translate(-30%, 10%);
  overflow: hidden;
  z-index: 5;
  opacity: 0.1;
}
