.WeatherPredictItem {
  padding: 18px 22px;
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #e7e7eb;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #1e213a;
  max-width: 120px;
  box-sizing: border-box;
  margin-right: 26px;
  margin-bottom: 32px;
}
.WeatherPredictItem img {
  object-fit: cover;
}
