.searchButton {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #e7e7eb;
  background: #3c47e9;
  padding: 11px;
  text-align: center;
  border: 3px solid #3c47e9;
  cursor: pointer;
  transition: 0.8s;
}
.searchButton:hover {
  background: none;
  transition: 0.8s;
}
.SearchBar {
  display: flex;
  justify-content: space-between;
  margin-bottom: 58px;
}
