.Content {
  background: #100e1d;
  padding: 100px;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 10;
}
.blockName {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  color: #e7e7eb;
  margin: 40px 0 8px 0;
}
.linkBottom {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0%);
  color: #A09FB1;
}
